.container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100vh;
  background-image: url("../img/spaghetti-1932466_1280.jpg");
  background-size: cover;
  background-position: center;
  filter: brightness(0.83);
}

.container h1 {
  color: aliceblue
}

.container label {
  color: aliceblue
}

.form {
  background-color: rgba(240, 240, 255, 0.2);
  border-radius: 20px;
  width: 550px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 30px;
  margin-top: 30px;
}
.form label {
  margin-top: 10px;
  font-size: 17px;
}
.form button{
  margin-top: 20px;

}

.button {
  background-color: rgb(234, 195, 20);
  color: black;
  padding: 6px 20px;
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-left: 10px;
  cursor: pointer;
  font-size: 15px;
  box-shadow: 0px 2px 4px rgba(230, 209, 51, 0.4);
  /* Sombra difuminada */

}

.button:hover {
  background-color: rgb(224, 208, 135)
}